div.Tabs {
  display: flex;
  flex-grow: 1;
}

div.Tabs input {
  display: none;
}

div.Tabs label {
  color: cadetblue;
  text-align: left;
  text-wrap: nowrap;
  text-align: center;
  font-size: 24px;
}

div.Tabs div :hover {
  text-decoration: underline;
  cursor: pointer;
}

div.Tabs input[type="radio"]:checked + label {
  text-decoration: underline;
}

div.Tabs div.TabItem {
  text-decoration:dashed;
}

div.TabMenu {
  display: flex;
  flex: 1;
}
